@import './../../libs/variables.scss';


.header{
    padding: 15px $sidePadding;
    display: flex;
    justify-content: space-between;
    
    h1{
        margin: 0;
    }

    nav{

        ul{
            display: flex;
            list-style-type: none;

            a{
                text-decoration: none;
                color: $main;
                padding: 15px;
                transition: $baseTransition;

                &:hover{
                    background-color: lighten($main, 40%);
                }

                &.active{
                    background-color: $main;
                    color: $white;
                }
            }
        }
    }
}